.footer{
    background-color: black;
    color: aliceblue;
    /* display: grid; */
}

.footer .ft-sec1{
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(auto-fill, minmax( 200px, 30%));
    padding: 20px 10px;
    text-align: left;
    justify-content: center;
}

.footer-links{
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(auto-fill, minmax( 200px, 30%));
    padding: 20px ;
    text-align: left;
    justify-content: center;
}